<template>
  <m-swipe swipeid="swipe" ref="swiper" :autoplay="2000" effect="slide" :loop="true">
    <div @click="goToDetail(news.id)" v-for="news in topNews" class="swiper-slide" slot="swiper-con">
      <img :src="news.image">
      <h3>{{ news.title }}</h3>
    </div>
  </m-swipe>
</template>

<script>
  export default {
    name: 'NewsPicture',
    data () {
      return {
        topNews: [],
        idx: 0
      }
    },
    methods: {
      fetchData () {
        this.$http.get('static/20170308.json').then((data) => {
          this.topNews = data.body.top_stories
        }, (err) => {
          console.log(err)
        })
      },
      goToDetail (id) {
        this.$router.push({
          name: 'article',
          params: {
            id: id
          }
        })
      }
    },
    created () {
      this.fetchData()
    }
  }
</script>

<style scoped>

.swiper-container{
    width: 100%;
}
.swiper-slide{
    position: relative;
    height: 250px;
    overflow: hidden;
}
.swiper-container-horizontal > .swiper-pagination-bullets{
    bottom: 30px;
    width: 95%;
    text-align: right;
}

.swiper-slide img{
  position: relative;
  width: 100%;
  height: 100%;
}
.swiper-slide h3{
  position: absolute;
  bottom: 50px;
  left: 5%;
  width: 70%;
  margin: 0;
  font-size: 18px;
  line-height: 20px;
  color: #fff;
  text-shadow: 1px 1px 10px rgba(0, 0, 0, .8);
}
.swiper-slide h3:before{
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  display: block;
  width: 100%;
  border: 1px solid #1abc9c;
}

    /*  */
  @media screen and (min-width: 768px) {
    .swiper-container{
      width: 640px;
      margin: 0 auto;
    }
    .swiper-slide{
      height: 400px;
    }
  }
</style>



