<template>
  <!--  -->
  <page-loading v-if="!collectNews.length&&!collectList.length"></page-loading>
  <div v-else>
    <!--  -->
    <div v-if="!!collectList.length" class="cus-collect container-fluid">
      <div class="list-group">
        <div v-for="msg in collectNews" class="col-md-5 col-md-push-1 col-xs-12 cus-item list-group-item">
        <!-- to vue docs   -->
          <router-link :to="{name: 'article', params: {id: msg.id}}">
            <div class="row">
              <div class="col-xs-8">
                <h3>{{ msg.title }}</h3>
              </div>
              <div class="col-xs-4">
                <img :src="msg.image" alt="" />
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import PageLoading from './PageLoading.vue'

  export default {
    name: 'NewsCollection',
    data () {
      return {
        collectNews: []
      }
    },
    components: {
      PageLoading
    },
    computed: {
      ...mapState({
        collectList: state => state.collectList
      })
    },
    methods: {
      fetchData () {
        this.collectList.forEach((ele, idx) => {
          this.$http.get('static/json/' + ele + '.json').then((data) => {
            this.collectNews.push(data.body)
          }, (err) => {
            console.log(err)
          })
        })
      }
    },
    created () {
      this.$store.dispatch('setCollectList') // 
      this.fetchData()
    }
  }
</script>

<style scoped>
  .cus-collect{
    background-color: #f3f3f3;
  }
  .cus-collect h2{
    font-size: 14px;
    color: #6c6c6c;
  }
  .cus-collect h3{
    padding: 5px;
    margin: 5px 0 10px;
    font-size: 16px;
    color: #2b2b2b;
    line-height: 1.42;
  }
  .cus-collect img {
    width: 86px;
    height: 75px;
  }
  .cus-item{
    height: 100px;
    margin-bottom: 10px;
    box-shadow: 0px 3px 8px #ccc;
  }
</style>



