import * as types from './mutation-types'

// 
export default {
  //  state.news,
  [types.STORE_NEWS_INFO] (state, news) {
    if (!localStorage.getItem('vue-news')) {
    } else {
      state.news = JSON.parse(localStorage.getItem('vue-news'))
    }
    // everynews
    if (state.news.every(function (ele) {
      return ele.id !== news.id
    })) {
      state.news.push(news)
    }
    localStorage.setItem('vue-news', JSON.stringify(state.news))
  },
  //  sate.curNews
  [types.SET_CURRENT_NEWS] (state, id) {
    state.news.forEach((ele, idx) => {
      if (ele.id === id) {
        state.curNews = state.news[idx]
      }
    })
  },
  // 
  [types.TOGGLE_SIDEBAR] (state) {
    state.isMaskShow = !state.isMaskShow
  },
  // 
  [types.TOGGLE_NIGHT_PATTERN] (state) {
    state.isNightPattern = !state.isNightPattern
  },
  // 
  [types.SET_COLLECT_LIST] (state) {
    if (!localStorage.getItem('vue-collectList')) {
    } else {
      state.collectList = JSON.parse(localStorage.getItem('vue-collectList'))
    }
    state.news.forEach((ele, idx) => {
      if (ele.isCollected) { //   
        if (state.collectList.indexOf(ele.id) === -1) {
          state.collectList.push(ele.id)
          localStorage.setItem('vue-news', JSON.stringify(state.news))
          localStorage.setItem('vue-collectList', JSON.stringify(state.collectList))
        }
      }
    })
  },
  // 
  [types.REDUCE_COLLECT_LIST] (state, id) {
    if (!localStorage.getItem('vue-collectList')) {
    } else {
      state.collectList = JSON.parse(localStorage.getItem('vue-collectList'))
    }
    state.collectList.splice(state.collectList.indexOf(id), 1) // ID1
    localStorage.setItem('vue-collectList', JSON.stringify(state.collectList)) //   
    localStorage.setItem('vue-news', JSON.stringify(state.news))
  },
  // 
  [types.CONTINUE_LOAD_MORE] (state) {
    state.isMoreShow = true
  },
  // 
  [types.STOP_LOAD_MORE] (state) {
    state.isMoreShow = false
  },
  // 
  [types.TOGGLE_LIKED_BOX] (state) {
    state.isBoxShow = !state.isBoxShow
    localStorage.setItem('vue-news', JSON.stringify(state.news))
  },
  // 
  [types.TOGGLE_CTRL_BOX] (state) {
    state.isCtrlShow = !state.isCtrlShow
  },
  // 
  [types.SET_USER_INFO] (state, userInfo) {
    if (!localStorage.getItem('vue-user')) { //  
      state.user.name = userInfo.name
      state.user.password = userInfo.password
      localStorage.setItem('vue-user', JSON.stringify(state.user))
    } else {                                //  
      state.user = JSON.parse(localStorage.getItem('vue-user'))
    }
  },
  // 
  [types.TOGGLE_LOGIN_STATE] (state) {
    state.isUserLogin = !state.isUserLogin
  },
  // 
  [types.TOGGLE_QRCODE_SHOW] (state) {
    state.isQRCodeShow = !state.isQRCodeShow
  },
  // 
  [types.SET_COLUMN_TITLE] (state, name) {
    state.columnTitle = name
  }
}



