<template>
  <div v-if="!!messages.length" class="cus-messgae container-fluid">
    <div class="list-group">
      <div @click="goToAboutMe" v-for="msg in messages" class="cus-item list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <p>{{ msg.text }}</p>
          </div>
          <div class="col-xs-3">
            <i @click="deleteMsg(this.index)" class="fa fa-times fa-2x"></i>
            <span>{{ msg.date }}</span>
          </div>
        </div>
      </div>
    </div>
    <button @click="deleteAll" v-if="messages.length > 1" class="btn btn-primary"></button>
  </div>
  <div v-else>
    <div class="jumbotron">
      <p></p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SystemMessage',
    data () {
      return {
        messages: [
          {
            text: ' --- ntnyq',
            date: '03/21'
          },
          {
            text: 'Lorem ipsum dolor sit amet, consectetur adipisicing.',
            date: '03/12'
          },
          {
            text: 'Lorem ipsum dolor sit amet.',
            date: '03/22'
          }
        ]
      }
    },
    methods: {
      deleteMsg (idx) {
        this.messages.splice(idx, 1)
      },
      deleteAll () {
        this.messages = []
      },
      goToAboutMe () {
        this.$router.push({
          path: '/aboutme'
        })
      }
    }
  }
</script>

<style scoped>
.cus-messgae .cus-item{
  position: relative;
  margin-bottom: 10px;
  padding: 10px;
  padding-top: 20px;
  border: 1px solid #aaa;
  box-shadow: 0 3px 10px #ccc;
  font-size: 16px;
  color: #333;
}
.cus-messgae .cus-item span{
  display: inline-block;
  margin-top: 10px;
  color: #888;
}
</style>



