<template>
  <div class="cus-loading">
    <div class="loading">
      <i></i>
      <i></i>
      <i></i>
      <i></i>
      <i></i>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PageLoading',
    data () {
      return {

      }
    }
  }
</script>

<style scoped>
  .cus-loading{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .loading{
    position: relative;
    width: 100px;
    height: 20px;
    margin: 200px auto;
  }
  .loading i{
    position:absolute;
    display:inline-block;
    width:20px;
    height:20px;
    border-radius:50%;
    background:#333;
    margin-right:10px;
  }
  .loading i:nth-child(1){
    background-color: #909e9f;
    animation:loading 2s linear 0s infinite;
  }
  .loading i:nth-child(2){
    background-color: #34bc98;
    animation:loading 2s linear -0.4s infinite;
  }
  .loading i:nth-child(3){
    background-color: #fe6553;
    animation:loading 2s linear -0.8s infinite;
  }
  .loading i:nth-child(4){
    background-color: #fee79b;
    animation:loading 2s linear -1.2s infinite;
  }
  .loading i:nth-child(5){
    background-color: #f6a0bb;
    animation:loading 2s linear -1.6s infinite;
  }
  /* loading  */
  @-webkit-keyframes loading {
    0%{
      left:100px;
      top:0;
    }
    80%{
      left:0;
      top:0;
    }
    85%{
      left:0;
      top:-20px;
      width:20px;
      height:20px;
    }
    90%{
      width:40px;
      height:20px;
    }
    95%{
      left:100px;
      top:-20px;
      width:20px;
      height:20px;
    }
    100%{
      left:100px;
      top:0;
    }
  }
  @keyframes loading {
    0%{
      left:100px;
      top:0;
    }
    80%{
      left:0;
      top:0;
    }
    85%{
      left:0;
      top:-20px;
      width:20px;
      height:20px;
    }
    90%{
      width:40px;
      height:20px;
    }
    95%{
      left:100px;
      top:-20px;
      width:20px;
      height:20px;
    }
    100%{
      left:100px;
      top:0;
    }
  }
</style>



