<template>
  <div class="cus-login container-fluid">
    <h3 class="h3 text-center"></h3>
    <form class="form-group" role="form">
      <label for="username"></label>
      <span>{{ usernameTip }}</span>
      <input v-model="username" class="form-control" type="text" placeholder="//" />
    </form>
    <form class="form-group" role="form">
      <label for="password"></label>
      <span>{{ passwordTip }}</span>
      <input v-model="password" class="form-control" type="password" placeholder="" />
    </form>
    <div class="cus-btns btn-group">
      <button @click="login" class="btn btn-primary">&nbsp;</button>
      <button  @click="registerMember" class="btn btn-info">&nbsp;</button>
    </div>
    <p></p>
  </div>
</template>

<script>

  import { mapState } from 'vuex'

  export default {
    name: 'UserLogin',
    data () {
      return {
        username: '',
        password: '',
        usernameTip: '',
        passwordTip: ''
      }
    },
    computed: {
      validateUserName () {
        const userReg = /^[a-zA-Z][a-zA-Z0-9_@.]{5,15}$/
        if (this.username.length < 6 || this.username.length > 16) {
          return '616'
        } else if (!userReg.test(this.username)) {
          return ''
        }
      },
      validatePassword () {
        const pwdReg = /^[a-zA-Z][(\w)!@#%]{9,15}$/  // \w
        if (this.password.length < 10 || this.password.length > 16) {
          return '1016'
        } else if (!pwdReg.test(this.password)) {
          return ''
        }
      },
      ...mapState({
        userInfo: state => state.user,
        isUserLogin: state => state.isUserLogin
      })
    },
    methods: {
      login () {
        if (!this.userInfo.name && !this.userInfo.password) {
          alert('')
        } else {
          if (this.username === this.userInfo.name && this.password === this.userInfo.password) {
            if (!this.isUserLogin) {
              alert('')
              this.$store.dispatch('toggleLoginState') //  
            }
          } else if (this.username !== this.userInfo.name) {
            this.usernameTip = '' //  
          } else if (this.password !== this.userInfo.password) {
            this.passwordTip = ''
          }
        }
      },
      registerMember () {
        this.usernameTip = this.validateUserName
        this.passwordTip = this.validatePassword
        if (!this.validateUserName && !this.validatePassword) {
          alert('')
          const userInfo = {
            name: this.username,
            password: this.password
          }
          this.$store.dispatch('setUserInfo', userInfo) // vuex
          this.username = '' // 
          this.password = ''
        }
      }
    }
  }
</script>

<style scoped>
  .cus-login{
    position: fixed;
    width: 100%;
    height: 100%;
    background: -webkit-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: -o-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: -moz-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    padding: 0;
  }
  .cus-login h3{
    margin: 80px auto 100px;
    font-family: "Comic Sans MS",cursive;
    font-size: 32px;
    font-weight: 600;
    color: #fff;
  }
  .cus-login label{
    font-size: 18px;
    font-weight: bold;
    color: #fff;
  }
  .cus-login span{
    margin-left: 10px;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1px;
    color: orange;
  }
  .cus-login input[type="text"],
  .cus-login input[type="password"]{
    height: 40px;
    margin-top: 5px;
    font-size: 16px;
  }
  .cus-login form{
    width: 240px;
    margin: 0 auto;
  }
  .cus-login form:nth-of-type(2){
    margin-top: 30px;
  }
  .cus-login .cus-btns{
    position: relative;
    left: 50%;
    width: 242px;
    margin-top: 20px;
    margin-left: -121px;
  }
  .cus-login button{
    padding: 10px 35px;
    margin: 10px;
    border: none;
    outline: none;
    font-size: 16px;
  }
  .cus-login p{
    width: 100%;
    margin: 80px 0 20px;
    font-size: 14px;
    text-align: center;
    color: #fff;
  }

  @media screen and (min-width: 768px) {
    .cus-login form{
      width: 480px;
    }
    .cus-login input[type="text"],
    .cus-login input[type="password"]{
      height: 60px;
      font-size: 30px;
    }
    .cus-login label{
      font-size: 24px;
    }
  }
</style>



