<template>
  <div :class="{night: isNightPattern}" @click="closeBox" class="cus-wrapper container-fluid">
    <news-header></news-header>
    <router-view></router-view>
  </div>
</template>

<script>
  import NewsHeader from './components/NewsHeader.vue'

  export default {
    name: 'App',
    components: {
      NewsHeader
    },
    computed: {
      isNightPattern () {
        return this.$store.state.isNightPattern
      },
      isCtrlShow () {
        return this.$store.state.isCtrlShow
      },
      isQRCodeShow () {
        return this.$store.state.isQRCodeShow
      }
    },
    methods: {
      closeBox () {
        if (this.isCtrlShow) { //  
          this.$store.dispatch('toggleCtrlBox')
        }
        if (this.isQRCodeShow) {
          this.$store.dispatch('toggleQRCodeShow')
        }
      }
    },
    created () {
      console.log('\n %c ntnyq %c http://github.com/ntnyq \n\n', 'color: #fff; background: #1abc9c; padding:5px 10px;border-radius: 5px 0 0 5px;', 'color: #34495e;background: #edeef0; padding:5px 0;border-radius: 0 5px 5px 0')
    }
  }
</script>

<style>
  html, body{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    /* allowteam */
    font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Tahoma,Simsun,sans-serif;
  }
  .cus-wrapper{
    width: 100%;
    overflow-x: hidden;
    padding: 0;
    padding-top: 50px;
  }
  img, a{
    border: none;
  }
  a:hover{
    border: none;
  }

  /*  */
  input, a, button{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  body{
    -webkit-touch-callout:none;
    -webkit-overflow-scrolling: touch;
  }

  @media screen and (min-width: 768px) {
    .cus-wrapper{
      padding-top: 80px;
    }
  }
  /*  */
  .night.cus-wrapper{
    /*background-color: #444;*/
  }
</style>



